.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.header {
  width: 100vw;
  z-index: 10;
  background-color: rgba(132, 161, 120, .65);
  padding-top: 12px;
  padding-bottom: 0;
  position: fixed;
  top: 0;
  left: 0;
}

.header > .container {
  display: flex;
}

.header-div {
  width: 100%;
  height: 37px;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.logo {
  position: absolute;
  top: 0;
  left: 0;
}

.header-logo-svg {
  width: 40px;
  height: 37px;
  fill: var(--main-yellow);
}

.logo:hover .header-logo-svg, .logo:focus .header-logo-svg {
  animation-name: changeSvgColorLogo;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}

@keyframes changeSvgColorLogo {
  0% {
    fill: var(--main-yellow);
  }

  50% {
    fill: #ff0;
  }

  100% {
    fill: var(--main-yellow);
  }
}

.site-nav, .header-button {
  display: none;
}

.header-modal-open {
  width: 32px;
  height: 32px;
  fill: var(--white);
  background-color: rgba(132, 161, 120, 0);
  border: 0;
  align-items: flex-end;
  padding: 0;
  display: flex;
  position: absolute;
  bottom: 0;
  right: 0;
}

