.hero-plate {
  animation-name: anime-plate;
  animation-duration: 1.8s;
  animation-timing-function: cubic-bezier(.02, .46, .68, .99);
}

.hero-icecream {
  transform-origin: bottom;
  animation-name: anime-icecream;
  animation-duration: 1s;
  animation-timing-function: linear;
}

@keyframes anime-plate {
  0% {
    opacity: 0;
    transform: rotate(-10deg)translate(-10px, -5px);
  }

  60% {
    opacity: 0;
    transform: rotate(-10deg)translate(-10px, -5px);
  }

  100% {
    opacity: 1;
    transform: rotate(0deg)translate(0);
  }
}

@keyframes anime-icecream {
  0% {
    opacity: 0;
    transform: rotate(0deg);
  }

  40% {
    opacity: 0;
    transform: rotate(.4deg);
  }

  100% {
    opacity: 1;
    transform: rotate(0deg);
  }
}

