.reviews-section {
  padding-bottom: 10px;
  padding-bottom: 390px;
  // background: var(--main-green);
  // background: linear-gradient(to top, #01010100 97%, var(--main-green) 84%);
}

.slider {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 18px;
}

.slider-acting {
  gap: 0px;
}
.reviews-slider-icecream img {
  border-radius: 18px;
}

.slide-complete {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.reviews-slider-icecream {
  max-width: 440px;
  border-radius: 18px;
}

.reviews-slider-profile {
  margin-bottom: 32px;
}

.round-image {
  border-radius: 50%;
}

.swiper-wrapper {
  z-index: 0;
}

.slider-acting {
  margin-bottom: 12px;
}

.reviews-slider-coment {
  margin-bottom: 32px;
}

.reviews-slider-coment p {
  max-width: 280px;
  background-color: var(--main-green);
  padding: 35px 39px;
  border-radius: 10px;
  font-size: 12px;
  line-height: 2.55;
  text-align: center;
  letter-spacing: 0.02em;
}

.profile-name p {
  color: var(--text-dark);
  font-weight: 500;
  font-size: 20px;
  line-height: 1.86;
}

.reviews-container {
  padding: 0 20px 0;
  position: relative;
  bottom: 50px;
}
.swiper-pagination-clickable .swiper-pagination-bullet {
  cursor: url(../images/cursor.svg), auto;
}
// ============================== Tablet ==================================
@media screen and (min-width: 768px) {
  .reviews-section {
    padding-bottom: 230px;
  }
}

@media screen and (min-width: 768px) {
  .reviews-container {
    padding: 0 32px 0;
    bottom: 145px;
  }
}

@media screen and (min-width: 768px) {
  .reviews-slider-icecream {
    max-width: 100%;
  }
}

@media screen and (min-width: 768px) {
  .slider {
    margin-bottom: 100px;
  }
}

@media screen and (min-width: 768px) {
  .slider-acting {
    margin-bottom: 32px;
  }
}

@media screen and (min-width: 768px) {
  .reviews-slider-coment {
    width: 100%;
  }
}

@media screen and (min-width: 768px) {
  .reviews-slider-coment p {
    max-width: 100%;
    padding: 28px 80px;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.86;
    letter-spacing: 0.04em;
  }
}

@media screen and (min-width: 768px) {
  .profile-name p {
    font-size: 16px;
  }
}

